
@import 'craftcms-sass/mixins';

.adjustment {
    padding-bottom: 10px;

    &-type {
        color: $lightTextColor;
        font-size: 12px;
        font-weight: bold;
        text-transform: uppercase;
    }

    .fields {
        display: flex;
        box-sizing: inherit;
        margin: 0 -5px;

        .field {
            margin: 0;
            width: 20%;
            padding: 0 5px;
            box-sizing: inherit;

            &.included,
            &.included-labels {
                width: 14%;

                .input {
                    display: flex;
                }
            }

            &.included-labels {
                display: flex;
                flex-direction: column;

                .input {
                    flex-grow: 1;
                    align-items: center;
                }
            }

            &.included {
                .input {
                    width: 100%;
                    justify-content: center;
                }
            }

            .input {
                &::after {
                    display: none !important;
                }

                .select {
                    width: 100%;
                    box-sizing: border-box;

                    select {
                        width: 100%;
                    }
                }

                input[type='text'] {
                    width: 100%;
                }

                .readonly {
                    background-color: $grey200;
                    color: $mediumTextColor;
                }
            }
        }
    }
}

.order-price {
    min-width: 160px;
}
