
@import 'craftcms-sass/mixins';
.btn-link-default {
    font-size: 1em;
}

.btn-link {
    border: 0;
    color: $linkColor;
    padding: 0;
    background: transparent;

    &:hover {
        cursor: pointer;
        text-decoration: underline;
    }
}

.btn-link--danger {
    color: $errorColor;
}
