
@import 'craftcms-sass/mixins';

.order-meta-container {
    position: relative;
}

.order-meta-container.loading::after {
    background: #fff;
    bottom: 0;
    content: '';
    display: block;
    left: 0;
    opacity: 0.5;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 2;
}

.order-meta .value {
    overflow: hidden;
    text-overflow: ellipsis;
    min-width: 0;
}

.order-number-value {
    display: flex;

    div {
        overflow: hidden;
        text-overflow: ellipsis;
        min-width: 0;
    }

    .hidden-input {
        width: 1px;
        height: 1px;
        overflow: hidden;
        input {
            margin: -3000px;
        }
    }

    .btn-link {
        @include margin-left(7px);
    }
}

.meta .data.order-meta-shipping-method .value {
    align-items: start;
    flex-direction: column;
}

.shipping-method-handle {
    color: $mediumTextColor;
}
