
@import 'craftcms-sass/mixins';

.line-item {
    transition: background-color 0.5s ease;
    position: relative;

    &-bg {
        transition: background-color 0.5s ease;
        height: 100%;
        top: 0;
        left: -24px;
        right: -24px;

        &.highlight-line-item {
            background: #fffff0;
        }
    }

    &-section {
        width: 20%;
    }

    &-price {
        width: 60%;
    }

    &-buttons::after {
        content: '.';
        display: block;
        height: 0;
        clear: both;
        visibility: hidden;
    }

    label {
        @include padding-right(10px);
        max-width: 100%;
    }
}
