
@import 'craftcms-sass/mixins';
/* PrismJS fix for .token conflict with Craftâ€™s styles */

.prism-editor-wrapper {
    pre[class*='language-'] {
        padding: 0;
        font-size: 14px;
        line-height: 19px;
        margin: 0;
        background: transparent;

        &:focus {
            outline: none;
        }
    }

    .token {
        background: transparent;
        border: 0;
        padding: 0;
        box-shadow: none;
    }
}

.options-form .field {
    margin: 0;
}

body.ltr .options-field-pad-side {
    padding-right: 14px;
}

body.rtl .options-field-pad-side {
    padding-left: 14px;
}

.options-form .force-height {
    height: 100%;
}

.line-item-options-list.bullets {
    list-style-position: inside;

    body.ltr &,
    body.rtl & {
        padding: 0;
    }
}

.line-item-option-key {
    color: $mediumDarkTextColor;
    font-weight: bold;
}

.line-item-option-value {
    overflow: auto;

    body.ltr & {
        padding-left: 0.25em;
    }

    body.rtl & {
        padding-right: 0.25em;
    }
}
