@import 'craftcms-sass/mixins';
$lightGrey: #eee;

@import '../base/craft-classes';
@import '../base/vue-select';
@import '../base/common';
@import './modal';

.order-box-sizing * {
  box-sizing: border-box;
}

.order-flex {
  display: flex;
}

.order-flex-grow {
  flex-grow: 1;
}

.order-flex-wrap {
  flex-wrap: wrap;
}

.order-opacity-50 {
  opacity: 0.5;
}

.orderedit-border-t {
  border-top: 1px solid;
}

.orderedit-border-b {
  border-bottom: 1px solid;
}

.orderedit-border-color {
  border-color: #eee;
}
