
.order-address-display {
    position: relative;

    .zilch {
        padding-bottom: 5rem;
        padding-top: 5rem;
    }

    .address-card .address-card-header-actions {
        display: none;
    }

    // Hide initial action button
    .card-actions {
        padding-right: 24px;
    }

    &--static .address-card:hover {
        cursor: initial;
        background-color: initial;
        border-color: #eee;
    }

    &-buttons {
        position: absolute;
        top: 1rem;
        right: 1rem;

        *:not(:last-child) {
            margin-right: 4px;
        }
    }

    .menubtn.action-btn {
        position: absolute;
        top: var(--m);
        right: var(--m);
        background-color: transparent;

        height: var(--touch-target-size);
        margin: 0 -4px;
        width: var(--touch-target-size);
    }
}
